button {
  padding: 0;
  &:focus {
    outline: none !important;
  }
}

.button {
  display: inline-block;
  text-decoration: none;
  background-color: $color--gold-2;
  position: relative;
  border: 0;
  font-family: $body--text__font;
  font-weight: 700;
  cursor: pointer;
  @include vendor(border-radius, 30px);
  @include vendor(transition, all 300ms ease);
  &--grey {
    background-color: $color--grey-dark;
  }
  &.max-content {
    height: max-content;
    margin-top: 10px;
  }
  &:hover {
    background: $color--red;
    // &:before{
    //   width: 100%;
    // }
  }
  // &:before{
  //   content: "";
  //   position: absolute;
  //   width: 0%;
  //   height: 100%;
  //   left: 0;
  //   background: $color--red;
  //   z-index: 1;
  //   @include vendor(transition, width ease 290ms);
  //   @include vendor(border-radius, 30px);
  // }
  span {
    display: flex;
    align-items: center;
    color: $body--text__color-light;
    padding: 0.45rem 1.2rem;
    position: relative;
    z-index: 2;
    font-family: $body--text__font-2;
    font-size: 14px;
    img {
      display: block;
      height: 12px;
      margin-left: 0.5rem;
    }
  }
}
