// - - - - - - - - - - - - - - - - - - -
// - - forms
// define global forms
input {
  outline: none;
}

.form {
  .group {
    position: relative;
    margin-bottom: 35px;
  }
  .select {
    &-block {
      select {
        border-bottom: 1px solid $color--gold !important;
        @include vendor(border-radius, 0 !important);
        font-size: 14px;
        color: $body--text__color;
        font-weight: normal;
        background: url(../images/ico-select-down-2.svg) center right 10px
          no-repeat #fff !important;
      }
    }
  }
  a {
    color: #7d060f;
    font-weight: bold;
  }
  &__title {
    font-size: 16px;
    color: #7d060f;
  }
  input {
    font-size: 14px;
    padding: 11px 10px 11px 5px;
    display: block;
    width: 100%;
    border: none;
    border-bottom: 1px solid $color--gold;
    background: transparent;
    &:focus {
      outline: none;
    }
  }

  /* LABEL ======================================= */
  label {
    color: $color--grey-flash-4;
    font-size: 14px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;

    &.on-top {
      font-size: 9px;
      top: -6px;
    }
  }

  /* active state */
  input:focus ~ label,
  input:valid ~ label,
  input[type="email"]:focus ~ label,
  input[type="email"]:valid ~ label {
    top: -10px;
    font-size: 10px;
    color: $body--text__color;
  }

  /* BOTTOM BARS ================================= */
  .bar {
    position: relative;
    display: block;
    width: 100%;
  }
  .bar:before,
  .bar:after {
    opacity: 0;
    content: "";
    height: 1px;
    width: 0;
    bottom: 0;
    position: absolute;
    background: $color--gold;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
  }
  .bar:before {
    left: 50%;
  }
  .bar:after {
    right: 50%;
  }

  /* active state */
  input:focus ~ .bar:before,
  input:focus ~ .bar:after {
    width: 50%;
  }

  /* HIGHLIGHTER ================================== */
  .highlight {
    position: absolute;
    height: 60%;
    width: 100%;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
  }

  /* active state */
  input:focus ~ .highlight {
    -webkit-animation: inputHighlighter 0.3s ease;
    -moz-animation: inputHighlighter 0.3s ease;
    animation: inputHighlighter 0.3s ease;
  }

  /* ANIMATIONS ================ */
  @-webkit-keyframes inputHighlighter {
    from {
      background: transparent;
    }
    to {
      width: 0;
      background: transparent;
    }
  }
  @-moz-keyframes inputHighlighter {
    from {
      background: transparent;
    }
    to {
      width: 0;
      background: transparent;
    }
  }
  @keyframes inputHighlighter {
    from {
      background: transparent;
    }
    to {
      width: 0;
      background: transparent;
    }
  }
}
