.section-landingpage {
  background-image: url(../images/content/img-bgsplashscreen.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
    height: 100vw;
  }

  width: 100%;
  height: 100vh;

  position: absolute;
  left: 0;
  top: 0;

  .notification {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 32px;
    @media screen and (max-width: 1023px) {
      display: none;
    }
  }

  @media screen and (min-width: 1024px) {
    background: none;
    * {
      &:not(.notification) {
        display: none;
      }
    }
  }

  .img-logo {
    display: inline-block;
    margin-bottom: 45px;
  }

  .img-phone {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    @media screen and (min-width: 320px) and (max-width: 568px) and (orientation: landscape) {
      max-height: 210px;
    }
    @media screen and (max-width: 320px) {
      max-width: 280px;
    }
  }

  .content {
    position: relative;
    width: 100%;
    max-width: 280px;
    text-align: center;
    margin: auto;
    margin-top: 50px;

    p {
      color: white;
      font-size: 24px;
      margin-bottom: 35px;
      line-height: 1.3;
      @media screen and (max-width: 320px) {
        font-size: 20px;
        line-height: 1.2;
      }
    }
  }
  .link-apps {
    width: max-content;
    margin: auto;
    li {
      display: inline-block;
      &:not(:last-child) {
        margin-right: 10px;
      }
      a {
        img {
          max-width: 136px;
          @media screen and (max-width: 320px) {
            max-width: 130px;
          }
        }
      }
    }
  }
}
