.footer{
  @include vendor(transition, all ease 250ms);
  &.menu-active{
    @include vendor(transform, translateX(-270px));
  }
  &__payment{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    p{
      font-size: 14px;
      color: $color--black-2;
    }
    img{
      margin-left: .7rem;
      display: block;
      width: 146px;
    }
  }
  &__link{
    a{
      display: block;
      font-size: 16px;
      color: $body--text__color;
      &:hover{
        color: $color--gold;
      }
    }
  }
  &__sosmed{
    display: flex;
    justify-content: flex-end;
    a{
      display: block;
      margin: .26rem;
      &:last-child{
        margin-right: 0;
      }
      img{
        display: block;
        width: 32px;;
      }
    }
  }
  &__divider{
    width: 100%;
    height: 1px;
    background-color: $color--black-2;
    margin: 20px 0 20px;
  }
  &__short-desc{
    font-size: 16px !important;
    margin-right: .5rem;
    line-height: 1.4;
  }
  &__address{
    @include bp-425(){
      margin-top: 4rem;
    }
    &-2{
      padding-right: 3.4rem;
      @include bp-768(){
        padding-right: 0;
      }
    }
  }
  &__top{
    background-image: radial-gradient(circle at 77% 94%, #ab1522, #762e2e);
    color: $body--text__color-light;
    position: relative;
    padding: 2.7rem 0;
    h3{
      font-weight: normal;
      font-size: 34px;
      line-height: 1.3;
    }
    .app-screenshot{
      position: absolute;
      right: 0;
      bottom: -2.7rem;
      img{
        display: block;
        width: 155px;
      }
    }
  }
  &__bottom{
    background-color: $color--white-flash;
    color: $body--text__color;
    padding: 3.9rem 0;
    line-height: 1.7;
    .title-section{
      margin-bottom: .8rem;
    }
    .logo{
      display: block;
      img{
        display: block;
        width: 144px;
      }
    }
    h4{
      font-size: 16px;
    }
    h5{
      font-weight: 600;
      font-size: 13px;
      margin-bottom: .35rem;
      line-height: 1.8;
    }
    .newsletter{
      position: relative;
      margin-left: 1.4rem;
      input{
        border: 0;
        border-bottom: 1px solid $color--gold;
        padding: .4rem;
        width: 100%;
        background: transparent;
        font-family: $body--text__font;
      }
      button{
        cursor: pointer;
        padding: .4rem;
        top: .2rem;
        right: 0;
        position: absolute;
        border: 0;
        background: transparent;
        img{
          display: block;
          width: 10px;
        }
      }
    }
  }
  &__copyright{
    p{
      font-size: 14px;
      color: $color--black-2;
      @include bp-768(){
        text-align: left;
        margin-top: .8rem;
      }
    }
  }
}