// - - Vendor
@mixin vendor($property, $value...) {
    -webkit-#{$property}: $value;
    -moz-#{$property}: $value;
    -ms-#{$property}: $value;
    -o-#{$property}: $value;
    #{$property}: $value;
}

// - - Breakpoints
@mixin bp-2560($max-min:max) {
    @if($max-min == 'max'){
        @media only screen and (max-width: 2560px) {
            @content;
        }
    }@else{
        @media only screen and (min-width: 2560px) {
            @content;
        }
    }
    
}

