.hero{
  //background-color: $color--white-flash;
  &--section{
    .hero{
      &__background{
        &:before{
          padding-top: (3 / 16) * 100%;
        }
      }
    }
  }
  &__background{
    position: relative;
    display: block;
    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: (251 / 640) * 100%;
    }
    > .inner {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-size: cover;
    }
  }
  .slider{
    &__pagination{
      .swiper-button-disabled{
        //opacity: 1;
      }
      .swiper-button-next,
      .swiper-button-prev{
        background-image: none;
        @include bp-425(){
          width: 15px;
        }
        img{
          width: 100%;
        }
      }
      .swiper-button-next{
        right: 45px;
        @include bp-425(){
          right: 15px !important;
        }
        @include bp-768(){
          right: 25px;
        }
      }
      .swiper-button-prev{
        left: 45px;
        @include bp-425(){
          left: 15px !important;
        }
        @include bp-768(){
          left: 25px;
        }
      }
    }
  }
  &__headline{
    display: flex;
    flex-flow: column;
    @include vendor(transform, translate(-19%, 0%));
    margin-top: 6%;
    h1{
      span{
        font-size: 92px;
        font-weight: normal;
        display: inline-block;
        font-family: $body--text__font-2;
        letter-spacing: 1px;
        color: $body--text__color-light;
        position: relative;
        padding: .25rem 3rem .75rem;
        &::before{
          content: "";
          display: block;
          width: 100%;
          height: 100%;
          background-color: $color--black;
          position: absolute;
          left: 0;
          top: 0;
          z-index: -1;
        }
      }
      &:last-child{
        margin-top: .85rem;
      }
    }
  }
  &__information{
    position: absolute;
    width: 100%;
    bottom: 0;
    color: $body--text__color-light;
  }
}

.slider{
  .swiper-pagination{
    bottom: 40px;
    @include bp-768(){
      bottom: 10px;
    }
    &-bullet{
      width: 11px;
      height: 11px;
      border: 2px solid $body--text__color-light;
      background-color: transparent;
      opacity: 1;
      &-active{
        background-color: $body--text__color-light;
      }
    }
  }
}

.offer{
  background-color: rgba(118, 46, 46, 0.8);
  padding: 1.8rem;
  h1{
    font-size: 48px;
  }
  p{
    font-size: 20px;
  }
}