        line-height: 1;
      }
      h2 {
        font-size: 20px;
        color: $color--red;
        text-align: right;
      }
    }
  }
  padding: 26px 24px;
  .form {
    background-color: transparent;
    input {
      margin-bottom: 16px;
    }
    p {
      margin-bottom: 36px;
    }
    .back {
      margin-right: 20px;
      color: #7d060f;
      cursor: pointer;
    }
    input[type="date"]:before {
      content: attr(placeholder) !important;
      color: #aaa;
      margin-right: 20px;
    }
    input[type="date"]:focus:before,
    input[type="date"]:valid:before {
      content: "";
    }
    input[type="date"] {
      margin-bottom: 0;
      // background-image: url(../images/ico-select-down-2.svg);
      // background-position: right center;
      // background-repeat: no-repeat;
      // background-size: 10px 10px;
    }
    select.custom {
      background-color: transparent;
      border: none;
      width: 100%;
      font-size: 14px;
      padding: 11px 10px 11px 5px;
      border-bottom: 1px solid $color--gold;
      outline: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;

      background-image: url(../images/ico-select-down-2.svg);
      background-position: right center;
      background-repeat: no-repeat;
      background-size: 10px 10px;
    }
    .checkbox-group,
    .radio-group {
      position: relative;
      margin-top: 10px;
      label.custom,
      label.custom-radio {
        position: relative;
        display: inline-block;
        width: 24px;
        height: 24px;
        border-radius: 5px;
        border: 2px solid #979797;
        margin-left: 0;
        &:after {
          content: "";
          width: 80%;
          height: 80%;
          position: absolute;
          left: 50%;
          top: 50%;
          background-color: #b39f66;
          transform: translate(-50%, -50%) scale(0);
          transition-duration: 0.3s;
          border-radius: 100%;
        }
      }
      label.custom-radio {
        border-radius: 100%;
        background-color: rgba(0, 0, 0, 0.5);
      }
      label {
        left: auto;
        top: auto;
        margin-left: 10px;
        pointer-events: all;
        font-size: 14px;
        cursor: pointer;
      }
      input[type="checkbox"],
      input[type="radio"] {
        opacity: 0;
        position: absolute;
        left: 0;
        top: 0;
        width: 0;
        height: 0;
        visibility: hidden;
      }
      input[type="checkbox"]:checked + label.custom {
        &:after {
          transform: translate(-50%, -50%) scale(1);
          border-radius: 3px;
        }
      }
      input[type="radio"]:checked + label.custom-radio {
        &:after {
