// - - pages
main {
  padding-top: 120px;
  @include bp-1024() {
    padding-top: 45px;
  }
  @include vendor(transition, all ease 250ms);
  &.menu-active {
    @include vendor(transform, translateX(-270px));
  }
}

.section-payment {
  @media screen and (max-width: 820px) {
    padding: 0.2rem 0 !important;
  }
}
.desktop-only {
  @media screen and (max-width: 820px) {
    display: none;
  }
}
.mobile-only {
  @media screen and (min-width: 821px) {
    display: none;
  }
}
.mobile-switch-column {
  @media screen and (max-width: 820px) {
    flex-direction: column-reverse;
  }
}
