// - - - - - - - - - - - - - - - - - - -
// - - fonts
// define global fonts

// - - - - - - - - - - - - - - - - - - -
// - - fonts use

// - - Roboto
@import url('https://fonts.google.com/?selection.family=Roboto:400,400i,700,700i&query=roboto');
@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed:400,400i,500,500i,700,700i|Roboto:400,400i,500,500i,700,700i,900');


// - - - - - - - - - - - - - - - - - - -
// - - descriptive fonts
$font--roboto: 'Roboto', sans-serif;
$font--roboto-condensed: 'Roboto Condensed', sans-serif;


// - - - - - - - - - - - - - - - - - - -
// - - functional fonts
$body--text__font: $font--roboto;
$body--text__font-2: $font--roboto-condensed;