// - - helper
// define global helper

// - - layout
.divider{
    margin: 20px 0;
    background: transparent;
    height: 1px;
    &--margin{
        &-small{
            margin: 10px;
        }
        &-small-extra{
            margin: 5px;
        }
        &-big{
            margin: 30px;
        }
        &-big-extra{
            margin: 45px;
        }
    }
    &--mobile{
        display: none;
        @include bp-768(){
            display: block;
        }
    }
}

.text-right{
    text-align: right;
}
.text-center{
    text-align: center;
}

.mobile-small-show{
    display: none;
    @include bp-425(){
        display: block;
    }
}
.mobile-show{
    display: none;
    @include bp-1024(){
        display: block;
    }
}
.mobile-hide{
    @include bp-1024(){
        display: none;
    }
}