.header{
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 15;
  height: 120px;
  background-color: $color--white;
  @include vendor(transition, all ease 250ms);
  @include vendor(box-shadow, 0 2px 3px 0 rgba(134, 134, 134, 0.5));
  &.menu-active{
    @include vendor(transform, translateX(-270px));
  }
  @include bp-1024(){
    height: 120px;
  }
  &-scroll-trigger{
    position: absolute;
    width: 100%;
    height: 2px;
    background: transparent;
    margin-top: 52vh;
  }
  &__top{
    //max-height: 32px;
    color: $body--text__color-light;
    background-color: $color--red;
    padding: .3rem 0;
    &-right{
      display: flex;
      justify-content: flex-end;
    }
    nav{
      font-family: $body--text__font-2;
      // margin-left: 3rem;
      a{
        color: $body--text__color-light;
        opacity: .5;
        &.active{
          font-weight: bold;
          opacity: 1;
        }
      }
    }
  }
  &__right{
    position: absolute;
    right: 15px;
    bottom: 0;
    display: flex;
    align-items: center;
    &--is-expand {
      width: 75%;
    }
    .search-form{
      margin-bottom: 16px;
    }
    nav{
      position: relative;
      margin-left: .8rem;
      padding-bottom: 16px;
      .oval{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 32px;
        height: 32px;
        background-color: $color--gold;
        @include vendor(border-radius, 50%);
        &:hover{
          background-color: $color--red-3;
        }
        img{
          width: 16px;
          display: block;
        }
        div {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          width: 32px;
          height: 32px;
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
          border-radius: 50%;
        }
      }
    }
  }
  &__wrapper{
    position: relative;
    display: flex;
    align-items: center;
    height: 88px;
  }
  .logo{
    display: block;
    margin-right: .9rem;
    @include vendor(transition, top ease 250ms);
    z-index: 2;
    img{
      display: block;
      width: 218px;
      @include vendor(transition, width ease 250ms);
      @include bp-1024(){
        width: 132px;
      }
    }
  }
}
.menu{
  position: relative;
  left: 25px;
  //right: 0;
  bottom: -15px;
  @include bp-1024(){
    display: none;
  }
  &-mobile{
    display: none;
    position: fixed;
    width: 270px;
    height: 100%;
    overflow-y: scroll;
    background-color: $color--red;
    z-index: 999;
    right: -270px;
    top: 0;
    @include vendor(transition, all ease 250ms);
    &.menu-active{
      right: 0px;
    }
    ul{
      .inner {
        padding-left: 13px;
        margin-bottom: .6rem;
        overflow: hidden;
        display: none;
        li{
          a{
            font-size: 13px;
            padding: .5rem 1.3rem;
          }
        }
        &.show {
          /*display: block;*/
        }
      }
      li{
        margin: .5em 0;
        a{
          display: block;
          text-decoration: none;
          color: $body--text__color-light;
          padding: .7rem 1.3rem;
          position: relative;
        }
        a.toggle {
          &::before,
          &::after{
            content: "";
            display: block;
            width: 8px;
            height: 1px;
            background-color: $color--white;
            position: absolute;
            right: 10px;
            top: 0;
            bottom: 0;
            margin: auto;
            @include vendor(transition, all ease 250ms);
          }
          &::before{
            @include vendor(transform, rotate(45deg));
            right: 15px;
          }
          &::after{
            @include vendor(transform, rotate(-45deg));
          }
          &.show{
            &::before{
              @include vendor(transform, rotate(-45deg));
            }
            &::after{
              @include vendor(transform, rotate(45deg));
            }
          }
        }
      }
    }
    @include bp-1024(){
      display: block;
    }
  }
  &-burger{
    display: none;
    width: 55px;
    height: 45px;
    //display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
    cursor: pointer;
    @include bp-1024(){
      display: flex;
    }
    &--active{
      span{
        background: transparent !important;
        &::before{
          top: 0 !important;
          @include vendor(transform, rotate(45deg));
        }
        &::after{
          bottom: 0 !important;
          @include vendor(transform, rotate(-45deg));
        }
      }
    }
    span{
      display: flex;
      width: 25px;
      height: 2px;
      background: $body--text__color;
      position: relative;
      flex-wrap: wrap;
      @include vendor(transition, all ease 250ms);
      &::before,
      &::after{
        content: "";
        display: block;
        width: 25px;
        height: 2px;
        background-color: $body--text__color;
        position: absolute;
        @include vendor(transition, all ease 250ms);
      }
      &::before{
        top: -7px;
      }
      &::after{
        bottom: -7px;
      }
    }
  }
  ul{
    display: flex;
    align-items: center;
    justify-content: center;
    li{
      display: flex;
      align-items: center;
      position: relative;
      &:hover{
        .submenu{
          opacity: 1;
          visibility: visible;
        }
      }
      a{
        display: block;
        padding: 1rem .85rem;
        text-decoration: none;
        color: $body--text__color;
        font-size: 16px;
        font-family: $body--text__font-2;
        font-weight: bold;
        text-transform: uppercase;
        &:hover,
        &.active{
          color: $color--gold;
        }
        &.has-submenu{
          position: relative;
          padding-right: 13px;
          &::before,
          &::after{
            content: "";
            display: block;
            width: 5px;
            height: 2px;
            background-color: $color--black;
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
          }
          &::before{
            @include vendor(transform, rotate(45deg));
            right: 3px;
          }
          &::after{
            @include vendor(transform, rotate(-45deg));
          }
        }
      }
      .divider{
        margin: 0 .8rem;
        background: $color--grey-flash;
        width: 24px;
      }
      .submenu{
        position: absolute;
        display: block;
        top: 48px;
        visibility: hidden;
        opacity: 0;
        width: 125px;
        @include vendor(transition, all ease 250ms);
        background-color: $body--text__color-light;
        box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, 0.31);
        border-radius: 5px;
        padding: .7rem 1rem;
        li{
          display: block;
          &:first-child{
            a{
              padding-top: 0;
            }
          }
          &:last-child{
            a{
              padding-bottom: 0;
              border-bottom: 0;
            }
          }
          a{
            padding: .55rem 0;
            position: relative;
            color: $body--text__color;
            border-bottom: 1px solid $color--grey-flash-3;
            &:hover,
            &.active{
              color: $color--gold;
            }
          }
        }
      }
    }
  }
}

.legal-info{
  display: flex;
  align-items: center;
  font-family: $body--text__font-2;
  span{
    display: block;
    font-weight: bold;
    margin-right: .4rem;
  }
  p{
    font-size: 12px;
    font-style: italic;
    border: 1px solid $color--red-2;
    border-left: 0;
    padding: .1rem .45rem;
    @include vendor(border-top-right-radius, 5px);
    @include vendor(border-bottom-right-radius, 5px);
  }
}